






















import Vue from 'vue';
import { Component, Prop } from 'vue-property-decorator';

@Component
export default class TournamentInfo extends Vue {
  @Prop({ required: true }) readonly value!: String[];

  data() {
    return {
      text: [
        'Nazwa turnieju',
        'Data turnieju',
        'Kraj',
        'Adres',
        'Limit uczestnikow',
        'Organizator',
        'System rozgrywek',
      ],
    };
  }
  setup() {
    const center = { lat: 40.689247, lng: -74.044502 };

    return { center };
  }
  get info() {
    return this.value;
  }
}
